import {Component, ContentChild, Input, OnInit, TemplateRef} from '@angular/core';
import {IPage} from '../ipage';
import {IPageLanguageContainer} from '../ipage-language-container';

@Component({
  selector: 'app-language-selector',
  templateUrl: './language-selector.component.html',
  styleUrls: ['./language-selector.component.scss']
})
export class LanguageSelectorComponent implements OnInit {
  public tabs: Array<string> = ['en', 'ru', 'uk'];
  public active: string = this.tabs[0];
  @Input() tpl: TemplateRef<{lang: string}>;
  public langObject: IPageLanguageContainer;


  @ContentChild(TemplateRef) public parentTemplate: TemplateRef<any>;

  constructor() { }

  ngOnInit() {
  }

}
